import * as React from "react";
import { ErrorMessage, Field, Formik, Form } from "formik";
import * as Yup from "yup";
import { navigate } from "gatsby";

const initialValues = {
  email: "",
  password: "",
};

const validationSchema = Yup.object({
  email: Yup.string().email("Invalid email").required("Required"),
  password: Yup.string().required("Required"),
});

const LoginForm = () => {
  const onSubmit = () => {
    navigate("/dashboard");
  };

  return (
    <div className="flex-column flex h-full w-full items-center justify-center">
      <div className="w-6/12 space-y-4 p-6 sm:p-8 md:space-y-6">
        <h1 className="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
          DigiFact
        </h1>
        <h1 className="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
          Sign in to your account
        </h1>

        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={onSubmit}
        >
          {({ isSubmitting, errors, touched, isValid }) => {
            return (
              <>
                {isSubmitting ? (
                  <div className="space-y-4">Loading ...</div>
                ) : (
                  <Form>
                    <div className="space-y-4 md:space-y-6">
                      <div>
                        <label
                          htmlFor="email"
                          className="mb-2 block text-sm font-medium text-gray-900 dark:text-white"
                        >
                          Your email
                        </label>
                        <Field
                          type="email"
                          name="email"
                          id="email"
                          className={`focus:ring-primary-600 focus:border-primary-600 block w-full rounded-lg border border-gray-300 bg-gray-50 p-2.5 text-gray-900 dark:border-gray-600 dark:bg-gray-700 dark:text-white dark:placeholder-gray-400 dark:focus:border-blue-500 dark:focus:ring-blue-500 ${
                            touched.email && errors.email
                              ? "border-red-500"
                              : ""
                          }`}
                          required=""
                        />
                        <ErrorMessage
                          name="email"
                          component="div"
                          className="my-2 text-red-700"
                        />
                      </div>
                      <div>
                        <label
                          htmlFor="password"
                          className="mb-2 block text-sm font-medium text-gray-900 dark:text-white"
                        >
                          Password
                        </label>
                        <Field
                          type="password"
                          name="password"
                          id="password"
                          className={`focus:ring-primary-600 focus:border-primary-600 block w-full rounded-lg border border-gray-300 bg-gray-50 p-2.5 text-gray-900 dark:border-gray-600 dark:bg-gray-700 dark:text-white dark:placeholder-gray-400 dark:focus:border-blue-500 dark:focus:ring-blue-500 ${
                            touched.password && errors.password
                              ? "border-red-500"
                              : ""
                          }`}
                          required=""
                        />
                        <ErrorMessage
                          name="password"
                          component="div"
                          className="my-2 text-red-700"
                        />
                      </div>
                      <div className="flex items-center justify-between">
                        <div className="flex items-start">
                          <div className="flex h-5 items-center"></div>
                        </div>
                        <a
                          href="/forgot-password"
                          className="text-primary-600 dark:text-primary-500 text-sm font-medium hover:underline"
                        >
                          Forgot password?
                        </a>
                      </div>
                      <button
                        type="submit"
                        disabled={!isValid || isSubmitting}
                        className="mb-2 me-2 w-full rounded-lg bg-blue-700 px-5 py-2.5 text-sm font-medium text-white hover:bg-blue-800 focus:outline-none focus:ring-4 focus:ring-blue-300 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
                      >
                        Sign In
                      </button>

                      <div className="m-auto w-4/5">
                        <div className="relative col-span-full flex justify-center before:absolute before:left-0 before:top-1/2 before:h-px before:w-full before:-translate-y-1/2 before:bg-[#E9E9E9]">
                          <h3 className="text-32 sm:text-16 relative z-10 inline-block bg-white px-4 text-center uppercase leading-snug tracking-[0.04em] sm:px-3 sm:tracking-wide">
                            or
                          </h3>
                        </div>
                      </div>

                      <p className="text-center text-lg font-light text-gray-500 dark:text-gray-400">
                        Donâ€™t have an account yet?{" "}
                        <a
                          href="/signup"
                          className="text-primary-600 dark:text-primary-500 font-medium underline hover:underline"
                        >
                          Sign up
                        </a>
                      </p>
                    </div>
                  </Form>
                )}
              </>
            );
          }}
        </Formik>
      </div>
    </div>
  );
};

export default LoginForm;
